import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { NavController, NavParams } from 'ionic-angular';
import { PinDialog } from '@ionic-native/pin-dialog';

@Component({
  selector: 'page-lockscreen',
  templateUrl: 'lock-screen.html'
})
export class LockScreenPage {

  pin: any;
  lock: boolean;
  //Fazer tentativas depois

  constructor(public navCtrl: NavController, public navParams: NavParams, private pinDialog: PinDialog, private alertCtrl: AlertController) {
    this.pin = navParams.get('pin');
    this.enterPin();
  }

  enterPin() {
    this.pinDialog.prompt(' ', 'Insira o seu PIN', ['OK'])
  .then(
    (result: any) => {
      if (result.buttonIndex == 1) {
        if (this.pin!== result.input1) {
          this.presentAlert("PIN errado","PIN errado. Tente outra vez", false);
        } else {
          this.presentAlert("PIN correto","Nice!", true);
        }
      }
    }
  );
  }

  presentAlert(title: string, message: string, correctPIN: boolean) {
    var butoes;
    if (!correctPIN) {
      butoes = [
        {
          text: 'OK',
          handler: () => {
            this.enterPin();
          }
        }
      ]
    } else {
      butoes = [
        {
          text: 'OK'
        }
      ]
    }
    
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: butoes
    });
    alert.present();
  }
}



