import { LockScreenPage } from './../lock-screen/lock-screen';
import { AlertController } from 'ionic-angular';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { PinDialog } from '@ionic-native/pin-dialog';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  pin: any;

  constructor(public navCtrl: NavController, private pinDialog: PinDialog, private alertCtrl: AlertController) {
    if (!this.pin) this.setPassword();
    else this.navCtrl.setRoot(LockScreenPage,{
      pin: this.pin
    });
  }

  setPassword() {
    var pin1;
    this.pinDialog.prompt('Este PIN poderá ser modificado mais tarde', 'Configure o seu PIN', ['OK'])
      .then(
        (result: any) => {
          if (result.buttonIndex == 1) {
            pin1=result.input1;
            this.confirmPassword(pin1);
          }
        }
      )
  }

  confirmPassword(pin1: any) {
    this.pinDialog.prompt('Insira o PIN outra vez', 'Confirme o PIN', ['OK', 'Cancelar'])
      .then(
        (result: any) => {
          if (result.buttonIndex == 1) {
            if (pin1!== result.input1) {
              this.presentAlert("PIN Errado!","O PIN que inseriu não é igual. Por favor tente outra vez.", false);
            } else {
              // SECURE STORAGE HERE (SAVE THE PASSWORD)
              this.pin=pin1;
              this.presentAlert("Configuração efetuada!","O seu PIN foi guardado. Pode modificar o PIN mais tarde em Configurar.",true);
            }
          } else this.setPassword(); 
        }
      );
  }

  presentAlert(title: string, message: string, correctPin: boolean) {
    var butoes;
    if (!correctPin) {
      butoes = [
        {
          text: 'Configurar outra vez',
          handler: () => {
            this.setPassword();
          }
        }
      ]
    } else {
      butoes = [
        {
          text: 'OK',
          handler: () => {
            this.navCtrl.setRoot(LockScreenPage,{
              pin: this.pin
            });
          }
        }
      ]
    }
    
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: butoes
    });
    alert.present();
  }
}



